import {
    Box, styled, Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow, Typography
} from "@mui/material";
import isFinite from "lodash/isFinite";
import { useEffect, useMemo, useState } from "react";
import { useParams } from "react-router";
import { useMiddleware } from "../../../../Application";
import { useAuth } from "../../../../auth";
import TableLoader from "../../../../components/TableLoader";
import pricify from "../../../../utils/pricify";
import Utils from "../../Utils";


const StyledTableRow = styled(TableRow)(({ theme }) => ({
    '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover
    },
    '&:last-child td, &:last-child th': {
        border: 0,
    },
}));


const ReservationTable = () => {

    const { inject } = useAuth();
    const middleware = useMiddleware('Online-Booking.Main');

    const params = useParams();

    const [isLoading, setLoading] = useState(true);
    const [reservation, setReservation] = useState();

    const _loadDetailsAsync = async () => {
        setLoading(true);
        const response = await middleware.details(params.reservationId);
        if (response.ok) {
            setReservation(response.reservation);
            setLoading(false);
        }
    }

    useEffect(() => {
        _loadDetailsAsync();
    }, [params.reservationId]);


    const total = useMemo(() => reservation && reservation.items ? Utils.getInvoiceTotal(reservation.items, reservation.factors) : 0, [reservation])


    if (isLoading) {
        return (
            <TableLoader size="small" />
        )
    }

    return (
        <TableContainer>
            <Table size="small" stickyHeader>
                <TableHead>
                    <TableRow>
                        <TableCell align="center">#</TableCell>
                        <TableCell align="center">کد</TableCell>
                        <TableCell>شرح</TableCell>
                        {inject('invoices', 'InvoiceItemsTable.HeadCells.AfterTitle')}
                        <TableCell align="center">تعداد</TableCell>
                        <TableCell align="center">فی <small>(ریال)</small></TableCell>
                        <TableCell align="center">تخفیف</TableCell>
                        <TableCell align="center">مجموع</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {reservation.items.map((item, idx) => (
                        <StyledTableRow key={item.id}>
                            <TableCell align="center">{idx + 1}</TableCell>
                            <TableCell align="center">{item.item.code}</TableCell>
                            <TableCell>{item.item.name}</TableCell>
                            {inject('invoices', 'InvoiceItemsTable.BodyCells.AfterTitle', { item })}
                            <TableCell align="center">{item.amount}</TableCell>
                            <TableCell align="center">{pricify(item.unitPrice)}</TableCell>
                            <TableCell align="center">{pricify(Utils.getItemDiscountPrice(item))}</TableCell>
                            <TableCell align="center">{pricify(Utils.getItemTotalPrice(item))}</TableCell>
                        </StyledTableRow>
                    ))}
                    <StyledTableRow>
                        <TableCell colSpan={100}>
                            <Box sx={{ width: '33.33%', ml: 'auto', display: 'flex' }}>
                                <Typography variant="subtitle2">جمع کل فاکتور</Typography>
                                <Box sx={{ flexGrow: 1 }} />
                                <Typography variant="subtitle2" sx={{ px: '16px' }}>{pricify(reservation.subtotal)}</Typography>
                            </Box>
                        </TableCell>
                    </StyledTableRow>
                    {reservation && reservation.factors?.map((factor, index) => (
                        <StyledTableRow key={factor.id}>
                            <TableCell colSpan={100}>
                                <Box sx={{ width: '33.33%', ml: 'auto', display: 'flex' }}>
                                    <Typography variant="subtitle2">{factor.title} {factor.unit === 'PERCENT' ? `(${factor.amount}%)` : ''}</Typography>
                                    <Box sx={{ flexGrow: 1 }} />
                                    <Typography variant="subtitle2" sx={{ px: '16px' }}>{pricify(factor.amount)}</Typography>
                                </Box>
                            </TableCell>
                        </StyledTableRow>
                    ))}
                    <StyledTableRow>
                        <TableCell colSpan={100}>
                            <Box sx={{ width: '33.33%', ml: 'auto', display: 'flex' }}>
                                <Typography variant="subtitle2">خالص فاکتور</Typography>
                                <Box sx={{ flexGrow: 1 }} />
                                <Typography variant="subtitle2" sx={{ px: '16px' }}><strong>{isFinite(total) ? pricify(total) : 0}</strong> <small>ریال</small></Typography>
                            </Box>
                        </TableCell>
                    </StyledTableRow>
                </TableBody>
            </Table>
        </TableContainer>
    )
}

export default ReservationTable